import React, { useState, useContext, useEffect } from "react";
import { AppContext } from "./AppContext";

export default function AddNoteForm() {
  const {
    colors,
    caseId,
    setCaseId,
    item,
    isComposeMode,
    matterTitle,
    setMatterTitle,
    client,
    setClient,
  } = useContext(AppContext);
  const [deadline, setDeadline] = useState("");
  const [result, setResult] = useState("");
  const [isHovered, setIsHovered] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  const spinnerStyle = {
    border: `3px solid ${colors.spinnerBorder}`,
    borderTop: `3px solid ${colors.spinnerBorderTop}`,
    borderRadius: "50%",
    width: "9px",
    height: "9px",
    animation: "spin 1s linear infinite",
    display: "inline-block",
    marginRight: "8px",
  };

  const keyframes = `
    @keyframes spin {
      0% { transform: rotate(0deg); }
      100% { transform: rotate(360deg); }
    }
  `;

  const getToday = () => {
    const today = new Date();
    const year = today.getFullYear();
    const month = String(today.getMonth() + 1).padStart(2, "0");
    const day = String(today.getDate()).padStart(2, "0");
    return `${year}-${month}-${day}`;
  };

  const generateCorrelationId = () => {
    return crypto.randomUUID().split("-")[0];
  };

  const addCorrelationIdIfMissing = async (currentItem, correlationId) => {
    return new Promise((resolve, reject) => {
      currentItem.body.getAsync(Office.CoercionType.Html, (asyncResult) => {
        if (asyncResult.status !== Office.AsyncResultStatus.Succeeded) {
          reject(asyncResult.error);
          return;
        }

        let bodyContent = asyncResult.value;

        const correlationRegex = /\[RBS-CID:([a-zA-Z0-9-]+)\]/g;

        const hasExisting = correlationRegex.test(bodyContent);

        if (hasExisting) {
          bodyContent = bodyContent.replace(correlationRegex, `[RBS-CID:${correlationId}]`);
        } else {
          const hiddenMarker = `
            <span style="display:none;max-height:0;overflow:hidden;font-size:0;mso-hide:all;">
              [RBS-CID:${correlationId}]
            </span>
          `;
          bodyContent = bodyContent + hiddenMarker;
        }

        currentItem.body.setAsync(
          bodyContent,
          { coercionType: Office.CoercionType.Html },
          (setResult) => {
            if (setResult.status === Office.AsyncResultStatus.Succeeded) {
              resolve(correlationId);
            } else {
              reject(setResult.error);
            }
          }
        );
      });
    });
  };

  const fetchCaseByCorrelationId = async (correlationId) => {
    try {
      const emailAddress = Office.context.mailbox.userProfile.emailAddress;
      const payload = {
        CorrelationID: correlationId,
        Email: emailAddress,
      };
      const response = await fetch(
        "https://outlook-rbs.it-bed.workers.dev/findCaseIDByCorrelationID",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(payload),
        }
      );

      if (response.ok) {
        const data = await response.json();
        const text = JSON.parse(data.text);
        if (text.CaseID) {
          setCaseId(text.CaseID);
          setMatterTitle(text.Matter ? text.Matter : "");
          setClient(text.Client ? text.Client : "");
        } else {
          setCaseId("");
        }
      } else {
        setCaseId("");
      }
    } catch (error) {
      setCaseId("");
    }
  };

  const tryFindCaseIdFromCompose = (item) => {
    item.body.getAsync(Office.CoercionType.Html, (result) => {
      if (result.status !== Office.AsyncResultStatus.Succeeded) return;

      const body = result.value;
      // Look for your CaseID or CID marker
      const cidMatch = body.match(/\[RBS-CID:([A-Za-z0-9-]+)\]/);

      if (cidMatch) {
        fetchCaseByCorrelationId(cidMatch[1]);
      } else {
        setCaseId("");
      }
    });
  };

  useEffect(() => {
    setDeadline(getToday());

    if (!item) {
      setCaseId("");
      setResult("");
      setMatterTitle("");
      setClient("");
      return;
    }

    setCaseId("");
    setResult("");
    setMatterTitle("");
    setClient("");

    if (!isComposeMode) {
      getCaseID();
    } else {
      tryFindCaseIdFromCompose(item);
    }
  }, [item, isComposeMode, getCaseID]);

  const getCaseID = async () => {
    setResult("");
    try {
      const currentItem = item;
      if (!currentItem) {
        return;
      }
      const messageId = currentItem.itemId;
      const emailAddress = Office.context.mailbox.userProfile.emailAddress;
      const conversationId = currentItem.conversationId;
      const internetMessageId = currentItem.internetMessageId;
      if (!messageId && !conversationId && !internetMessageId) {
        return;
      }
      const payload = {
        MessageID: messageId,
        Email: emailAddress,
        ConversationID: conversationId,
        InternetMessageID: internetMessageId,
      };
      const response = await fetch("https://outlook-rbs.it-bed.workers.dev/findCaseID", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(payload),
      });
      if (response.ok) {
        const data = await response.json();
        const text = JSON.parse(data.text);

        setCaseId((prev) => {
          if (prev.trim() === "") {
            return text.CaseID ? text.CaseID : "";
          }
          return prev;
        });

        setMatterTitle(text.Matter ? text.Matter : "");
        setClient(text.Client ? text.Client : "");

        if (text.Users && Array.isArray(text.Users) && text.Users.length > 0) {
          setResult(`A note has already been created for this email by: ${text.Users.join(", ")}`);
        } else {
          setResult("");
        }
      } else {
        const text = await response.text();
        setResult("❌ Error: " + text);
      }
    } catch (error) {
      setResult("❌ Error: " + error.message);
    }
  };

  const addNote = async () => {
    try {
      if (isLoading) return;

      if (!caseId || !deadline) {
        setResult("❌ Please fill in all fields.");
        return;
      }
      const currentItem = item;
      if (!currentItem) {
        setResult("❌ No email item found.");
        return;
      }

      const messageId = currentItem.itemId;
      const emailAddress = Office.context.mailbox.userProfile.emailAddress;
      if (!messageId) {
        setResult("❌ Cannot add note for draft email.");
        return;
      }
      setResult("");
      setIsLoading(true);

      const payload = {
        CaseID: caseId,
        Deadline: deadline,
        MessageID: messageId,
        Email: emailAddress,
      };
      const response = await fetch("https://outlook-rbs.it-bed.workers.dev/createNote", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(payload),
      });
      if (response.ok) {
        setIsLoading(false);
        const data = await response.json();
        const text = JSON.parse(data.text);
        if ("Error" in text) {
          setResult("❌ Error: " + text.Error);
          return;
        }
        if (!("Result" in text) || text.Result !== "Success") {
          setResult("❌ Error: Cannot connect to server.");
          return;
        }

        setResult("✅ Note created successfully!");
      } else {
        setIsLoading(false);
        const text = await response.text();
        setResult("❌ Error: " + text);
      }
    } catch (error) {
      setIsLoading(false);
      setResult("❌ Error: " + error.message);
    }
  };

  const sendAndAddNote = async () => {
    if (isLoading) return;

    if (!caseId || !deadline) {
      setResult("❌ Please fill in all fields.");
      return;
    }

    const currentItem = item;
    if (!currentItem) {
      setResult("❌ No email item found.");
      return;
    }

    setResult("");
    setIsLoading(true);

    const correlationId = generateCorrelationId();
    await addCorrelationIdIfMissing(currentItem, correlationId);

    const emailAddress = Office.context.mailbox.userProfile.emailAddress;
    const payload = {
      CaseID: caseId,
      Deadline: deadline,
      Email: emailAddress,
      CorrelationID: correlationId,
    };

    const response = await fetch("https://outlook-rbs.it-bed.workers.dev/createNoteFromCompose", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(payload),
    });

    if (response.ok) {
      setIsLoading(false);
      const data = await response.json();
      const text = JSON.parse(data.text);
      if ("Error" in text) {
        setResult("❌ Error: " + text.Error);
        return;
      }

      setResult("✅ Note created successfully!");
    } else {
      setIsLoading(false);
      const text = await response.text();

      setResult("❌ Error: " + text);
    }

    await new Promise((resolve, reject) => {
      currentItem.sendAsync((result) => {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
          resolve();
        } else {
          reject(result.error);
        }
      });
    });
  };

  const mainButtonStyles = {
    padding: "12px 15px",
    borderRadius: "6px",
    border: "none",
    color: "#fff",
    fontWeight: "bold",
    fontSize: "14px",
    transition: "background-color 0.2s ease, transform 0.1s ease",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    width: "100%",
    backgroundColor: isLoading
      ? colors.primaryColorHover
      : isHovered
        ? colors.primaryColorHover
        : colors.primaryColor,
    cursor: isLoading ? "not-allowed" : "pointer",
  };

  const buttonText = isComposeMode ? "Send Email and Add Note" : "Add Note";
  const buttonAction = isComposeMode ? sendAndAddNote : addNote;

  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "flex-start",
        fontFamily: "Roboto, 'Segoe UI', sans-serif",
        backgroundColor: "transparent",
        margin: 0,
        padding: "0 10px 10px 10px",
        color: colors.textColor,
      }}
    >
      <style>{keyframes}</style>

      <div
        style={{
          width: "100%",
          maxWidth: "480px",
          padding: "0 30px 10px 30px",
        }}
      >
        <h2
          style={{
            textAlign: "center",
            marginBottom: "30px",
            marginTop: "0",
            color: colors.textColor,
          }}
        >
          Add New Note
        </h2>

        {/* Input Group 1: CaseID */}
        <div style={{ marginBottom: "20px" }}>
          <label
            style={{
              display: "block",
              marginBottom: "8px",
              fontWeight: "600",
              color: colors.labelColor,
              fontSize: "14px",
            }}
          >
            Case ID
          </label>
          <input
            type="text"
            value={caseId}
            onChange={(e) => setCaseId(e.target.value)}
            placeholder="e.g., T12345"
            style={{
              width: "100%",
              padding: "12px 12px",
              borderRadius: "6px",
              border: `1px solid ${colors.borderColor}`,
              boxSizing: "border-box",
              fontSize: "14px",
              outline: "none",
              color: colors.inputColor,
              backgroundColor: colors.inputBg,
            }}
            onFocus={(e) => (e.target.style.borderColor = colors.primaryColor)}
            onBlur={(e) => (e.target.style.borderColor = colors.borderColor)}
          />
        </div>
        {/* --- Compact Case Details Section --- */}
        {(matterTitle || client) && (
          <div
            style={{
              marginBottom: "15px",
              padding: "8px 12px",
              borderRadius: "6px",
              backgroundColor: colors.detailsBg,
              border: `1px solid ${colors.detailsBorder}`,
              fontSize: "12px",
              lineHeight: "1.4",
            }}
          >
            {matterTitle && (
              <div style={{ marginBottom: "4px" }}>
                <span style={{ color: colors.labelColor, fontWeight: "400", marginRight: "6px" }}>
                  Matter:
                </span>
                <span style={{ fontWeight: "500" }}>{matterTitle}</span>
              </div>
            )}
            {client && (
              <div>
                <span style={{ color: colors.labelColor, fontWeight: "400", marginRight: "6px" }}>
                  Clients:
                </span>
                <span style={{ fontWeight: "500" }}>{client}</span>
              </div>
            )}
          </div>
        )}

        {/* Input Group 2: Deadline */}
        <div style={{ marginBottom: "30px" }}>
          <label
            style={{
              display: "block",
              marginBottom: "8px",
              fontWeight: "600",
              color: colors.labelColor,
              fontSize: "14px",
            }}
          >
            Deadline
          </label>
          <input
            type="date"
            value={deadline}
            onChange={(e) => setDeadline(e.target.value)}
            style={{
              width: "100%",
              padding: "12px 12px",
              borderRadius: "6px",
              border: `1px solid ${colors.borderColor}`,
              boxSizing: "border-box",
              fontSize: "14px",
              outline: "none",
              color: colors.inputColor,
              backgroundColor: colors.inputBg,
            }}
            onFocus={(e) => (e.target.style.borderColor = colors.primaryColor)}
            onBlur={(e) => (e.target.style.borderColor = colors.borderColor)}
          />
        </div>
        <div style={{ display: "flex", gap: "10px", marginBottom: "20px" }}>
          <button
            onClick={buttonAction}
            disabled={isLoading}
            style={mainButtonStyles}
            onMouseEnter={() => setIsHovered(true)}
            onMouseLeave={() => setIsHovered(false)}
            onMouseDown={(e) => (e.currentTarget.style.transform = "scale(0.98)")}
            onMouseUp={(e) => (e.currentTarget.style.transform = "scale(1)")}
          >
            {isLoading && <span style={spinnerStyle}></span>}
            {isLoading ? (isComposeMode ? "Sending & Saving..." : "Saving...") : buttonText}
          </button>
        </div>

        <div
          style={{
            textAlign: "center",
            fontSize: "14px",
            color: result.startsWith("❌") ? colors.resultErrorColor : colors.resultSuccessColor,
            minHeight: "20px",
          }}
        >
          <span>{result}</span>
        </div>
      </div>
    </div>
  );
}
