import React, { useState } from 'react';
import { AppContext } from './AppContext';

// --- Header Component (Adapted for compatibility) ---
const Header = ({ title, logo, message }) => {
  const { colors } = React.useContext(AppContext); 
  // Styles translated from the Fluent UI "makeStyles" request
  const headerStyle = {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    paddingTop: "10px",
    backgroundColor: "transparent", 
    width: "100%",
  };

  const messageStyle = {
    marginTop: "5px",
    marginBottom: "15px",
    fontSize: "42px", 
    fontWeight: "400", 
    color: colors.textColor,
    textAlign: "center",
    fontFamily: "'Segoe UI', sans-serif",
  };

  return (
    <section style={headerStyle}>
      <img 
        src={logo} 
        alt={title} 
        style={{ width: "90px", height: "auto" }}
        onError={(e) => {
          e.target.onerror = null; 
          e.target.style.display = 'none';
          e.target.parentNode.innerHTML += '<span style="font-size: 50px;">⚖️</span>'; 
        }}
      />
      <h1 style={messageStyle}>{message}</h1>
    </section>
  );
};

export default Header;